.invite-btn {
    background: rgb(46, 66, 182);
    border-radius: 20px;
    font-family: Arial, Helvetica, sans-serif;
    box-shadow: 1px 1px 10px 1px rgba(41, 94, 238, 0.644);
}

.alert-btn {
    background: rgb(78, 182, 9);
    border-radius: 20px;
    font-family: Arial, Helvetica, sans-serif;
    box-shadow: 1px 1px 10px 1px rgba(172, 255, 147, 0.616);
}

.alert-btn:active {
    box-shadow: none;
}

.invite-btn:active {
    box-shadow: none;
}

.bannerSecond{
    width: 90vw;
    margin: 0 auto;
}

.snowContainer { 
    width: 100%; 
    height: 100%; 
    position: absolute; 
    top: 0; 
    left: 0; 
    z-index: 0;
    overflow: visible;
}

.snow {
    width: 100%; 
    height: 100%; 
    background-image: 
    url("https://yraaa.ru/_pu/27/15825154.png"), 
    url("https://yraaa.ru/_pu/27/89961317.png"), 
    url("https://yraaa.ru/_pu/27/21791255.png"); 
    animation: snow 10s linear infinite;
}

@keyframes snow {
    0% {
        background-position: 0px 0px, 0px 0px, 0px 0px;
    } 
    100% {
        background-position: 500px 1000px, 400px 400px, 300px 300px;
    }
}